.steps {
  padding-top: 120px;
  padding-bottom: 117px;
  background-color: var(--light-color);

  @include small-tablet {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  &__title {
    margin: 0 auto;
    max-width: 720px;
    font-size: 30px;
    font-style: normal;
    font-weight: 400;
    line-height: 38px; /* 126.667% */
    letter-spacing: 1.2px;
    text-align: center;
    margin-bottom: 57px;

    @include small-tablet {
      font-size: 24px;
      line-height: 32px;
    }
  }

  &__list{
    //https://developer.mozilla.org/en-US/docs/Web/CSS/CSS_counter_styles/Using_CSS_counters
    counter-reset: steps;

    justify-content: space-between;
    margin: 0 auto;
    max-width: 758px;

    @include small-tablet {
      flex-wrap: wrap;
      gap: calc(var(--offset) / 2);
    }
  }

  &__descr {
    max-width: 142px;
    font-size: var(--second--size);
    line-height: 24px;
    color: rgba(53, 83, 98, 0.80);
    text-align: center;
  }

}

.steps__item {
  position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include small-tablet {
      width: 100%;
    }


    &::before {
      counter-increment: steps;
      content: counter(steps);

      display: flex;
      align-items: center;
      justify-content: center;

      width: 48px;
      height: 48px;
      border-radius: 999px;
      background-color: #014c48;

      font-size: 16px;
      line-height: 30px;
      color: var(--light-color);
    }

    &--line{
      flex-grow: 1;

      &::before{
        display: none;
      }

      &::after {
        content: "";
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        margin-top: -40px;
        width: 100%;
        height: 1px;
        background: rgba(53, 83, 98, 0.20);

        @include small-tablet {
          display: none;
        }
    }


  }
}
