.approach {

  &__top{
    padding: 80px 0;
    background: #F7F8F8;

    @include small-tablet {
      padding: 60px 0;
    }
  }

  &__left {
    grid-column: 5 span;

    @include big-desktop {

      grid-column: 12 span;
      max-width: 90%;
      margin: 0 auto;
      margin-bottom: var(--offset);
    }

    @include small-tablet {
      grid-column: 4 span;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 13px;
    font-size: 22px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px; /* 100% */
    color: var(--dark-color);
  }

  &__descr {
    margin-bottom: 37px;
    color: var(--text-color);
    font-size: var(--second--size);
    font-style: normal;
    font-weight: 400;
    line-height: 24px; /* 160% */

  }

  &__btn {
    color: #00413E;
    border: 1px solid rgba(53, 83, 98, 0.20);
  }

  &__right {
    grid-column: 7 / 6 span;
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    gap: 8px;

    @include big-desktop {
      grid-column: 12 span;
      justify-content: center;

    }

    @include small-tablet {
      grid-column: 4 span;

      flex-wrap: wrap;
    }
  }

  &__bottom{
    padding-top: 24px;
    padding-bottom: 21px;
    background: var(--light-color);
  }

  &__item{
    grid-column: 2 span;
  }

  &__link {
    display: block;
    padding: 17px;
    padding-left: 32px;
    border-top: 1px solid rgba(53, 83, 98, 0.30);;
    color: var(--text-color);
    font-size: var(--second-size);
    line-height: 24px;
    transition: color .3s ease-in-out, border-color .3s ease-in-out;

    background-position: left top 17px;
    background-repeat: no-repeat;
    background-size: 24px 24px;

    &--ted {
      background-image: url('../img/approach-icon-1.png');
    }
    &--nyt {
      background-image: url('../img/approach-icon-2.png');
    }
    &--scoop {
      background-image: url('../img/approach-icon-3.png');
    }
    &--press {
      background-image: url('../img/approach-icon-4.png');
    }
    &--time {
      background-image: url('../img/approach-icon-5.png');
    }
    &--freethink {
      background-image: url('../img/approach-icon-6.png');
    }

    &:focus {
      outline: none;
    }

    @include hover {
      border-color: var(--accent-color);
      color: var(--accent-color);
    }

    &:active {
      border-color: var(--active-color);
      color: var(--active-color);
    }
  }
}

.approach__link.focus-visible {
  border-color: var(--active-color);
}
