.press {
  padding-top: 87px;
  padding-bottom: 87px;

  @include small-tablet {
    padding-top: 60px;
    padding-bottom: 60px;
  }
  
  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 33px;

    @include small-tablet {
      flex-wrap: wrap;
      gap: 20px;
    }
  }

  &__title {
    color: var(--dark-color);
    font-family: var(--second-family);
    font-size: 27px;
    line-height: 38px; /* 140.741% */
    letter-spacing: 1.2px;
  }

  &__link {
    color: var(--dark-color);
    border: 1px solid rgba(53, 83, 98, 0.20);


  }

  &__list {
    row-gap: var(--offset);
  }

  &__item {
    grid-column: 4 span;

  }
}

.press-card {
  min-height: 145px;
  position: relative;

  @include small-tablet {
    min-height: 130px;
  }

  &__top {
    display: flex;
    align-items: center;
    gap: 16px;
    margin-bottom: 11px;
    border-top: 1px solid rgba(53, 83, 98, 0.30);

  }

  &__author {
    color: var(--text-color);
    font-size: 11px;
    line-height: 16px; /* 145.455% */
    letter-spacing: 0.5px;
    text-transform: uppercase;

    padding-left: 32px;
    padding: 18px 0 0px 32px;

    color: var(--text-color);
    font-size: var(--second-size);
    line-height: 24px;
    transition: color .3s ease-in-out, border-color .3s ease-in-out;

    background-position: left top 17px;
    background-repeat: no-repeat;
    background-size: 24px 24px;

    &--ted {
      background-image: url('../img/press-icon-1.png');
    }
    &--news {
      background-image: url('../img/press-icon-2.png');
    }
    &--justice {
      background-image: url('../img/press-icon-3.png');
    }
    &--nyt {
      background-image: url('../img/press-icon-4.png');
    }
    &--corrections {
      background-image: url('../img/press-icon-5.png');
    }
    &--crime {
      background-image: url('../img/press-icon-6.png');
    }

  }

  &__date {
    color: var(--text-color);
    font-size: 11px;
    line-height: 16px; /* 145.455% */
    letter-spacing: 0.5px;
    text-transform: uppercase;
    margin-top: 18px;
  }

  &__title {
    color: #00413E;
    font-size: 17px;
    line-height: 24.65px; /* 145% */
  }

  &__link {
    transition: color .3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--accent-color);
    }

    &:active {
      color: var(--active-color);
    }
  }

  &__link::after {
    content: '';
    position: absolute;
    inset: 0;
  }
}

.press-card__link.focus-visible {
  &::after {
    outline: 1px solid var(--active-color);
  }

}
