.footer {
  padding-top: 112px;
  background: #001414;
  color: #EDECEB;

  @include small-tablet {
    padding-top: 60px;
  }

  &__top {
    padding-bottom: 137px;

    @include small-tablet {
      padding-bottom: 50px;
    }
  }

  &__column {

    grid-column: 2 span;

    @include small-tablet {
      margin-bottom: var(--offset);
    }

    &:last-child {
      grid-column: 11 / 2 span;

      @include small-tablet {
        grid-column: 2 span;
      }
    }
  }

  &__title {
    color: #7D9798;
    font-size: 11px;
    line-height: 16px; /* 145.455% */
    letter-spacing: 1px;
    text-transform: uppercase;


    &::after {
      content: "";
      display: block;
      width: 16px;
      height: 1px;
      background: #25B895;
      margin: 24px 0 23px;
    }
  }

  &__link {
    color: #EDECEB;
    font-size: var(--second--size);
    line-height: 30px; /* 200% */
    transition: color .3s ease-in-out, border-color .3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--accent-color);
    }

    &:active {
      color: var(--active-color);
    }
  }

  &__bottom {
    padding: 40px 0 63px;
  }

  &__container {
    justify-content: space-between;
    align-items: flex-start;
  }
}

.footer__sublist {
  gap: 8px;
  margin-bottom: 8px;
  background: #000F0F;


  &-link {
    color: #E5ECEC;
    font-size: 10px;
    line-height: 10px; /* 100% */
    transition: color .3s ease-in-out, border-color .3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--accent-color);
    }

    &:active {
      color: var(--active-color);
    }

  }

  &-copy{
    color: #E5ECEC;
    font-size: 9px;
    line-height: 10px; /* 111.111% */
  }

  &-rights {
    color: #E5ECEC;
    font-size: 10px;
    line-height: 10px; /* 100% */
  }
}

.footer__link.focus-visible {
  border-color: var(--active-color);
}
.footer__sublist-link.focus-visible {
  border-color: var(--active-color);
}
