.info {
  display: flex;
  height: 680px;
  position: relative;
  background: #001414;

  @include tablet {
    height: auto;
    padding: 60px 0;
  }

  &::before {
    content: '';
    flex-shrink: 0;
    height: 100%;
    width: 287px;
    background-image: url('../img/info-1.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    @include tablet {
      display: none;
    }
  }

  &::after {
    content: '';
    flex-shrink: 0;
    height: 100%;
    width: 287px;
    background-image: url('../img/info-2.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    @include tablet {
      display: none;
    }
  }

  &__content {
    border-left: 1px solid #001414;
    border-right: 1px solid #001414;
    padding: 0 82px;
    color: #FFF;
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-grow: 1;

    @include tablet {
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  &__title {
     margin: 0;
    font-size: 22px;
    line-height: 22px; /* 100% */
    margin-bottom: 21px;
  }

  &__descr {
    font-size: var(--second--size);
    line-height: 24px; /* 160% */
  }
}
