.hero {
  display: flex;
  flex-direction: column;
  min-height: 776px;
  padding-top: 190px;
  padding-bottom: 40px;
  background-image: url('../img/hero.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  color: var(--light-color);

  &__content {
    grid-column: 6 span;

    @include small-tablet {
      grid-column: 4 span;
    }

  }
  &__container {
    margin-bottom: var(--offset);
  }

  &__bottom-container {
    width: 100%;
    margin-top: auto;
  }

  &__title{
    margin: 0;
    margin-bottom: 29px;
    font-family: var(--second-family);
    font-size: 38px;
    letter-spacing: 1.2px;
    line-height: 44px;

    @include tablet {
      font-size: 32px;
      line-height: 40px;
    }
  }

  &__descr{
    font-size: 18px;
    margin-bottom: 29px;
    line-height: 28px;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 15px;
    font-size: var(--second-size);
    font-style: normal;
    font-weight: 400;
    line-height: 24px;

  }

  &__list {
    @include small-tablet {
      row-gap: calc(var(--offset) / 2);
    }
  }

  &__item{
    grid-column: 2 span;
  }

  &__link {
    display: block;
    padding-top: 17px;
    border-top: 1px solid rgba(255, 255, 255, 0.60);
    color: inherit;
    font-size: var(--second-size);
    line-height: 24px;
    transition: color .3s ease-in-out, border-color .3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      border-color: var(--accent-color);
      color: var(--accent-color);
    }

    &:active {
      border-color: var(--active-color);
      color: var(--active-color);
    }
  }
}

.hero__link.focus-visible {
  border-color: var(--active-color);
}
