.where {
  padding: 146px 0 133px;
  background: #001F1F;
  color: var(--light-color);

  @include small-tablet {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  &__content {
    grid-column: 4 span;

    @include small-tablet {
      margin-bottom: var(--offset);
    }
  }

  &__map {
    grid-column: 7 / 5 span;

    @include small-tablet {
      grid-column: 4 span;
    }
  }

  &__title {
    color: #FAFAFA;
    font-family: var(--second-family);
    font-size: 36px;
    line-height: 52px; /* 144.444% */
    letter-spacing: 1.2px;
    margin-bottom: 43px;
  }

  &__descr {
    color: #FAFAFA;
    font-size: 18px;
    line-height: 28px; /* 155.556% */
    margin-bottom: 37px;
  }

  &__btn {
    background: #00665F;
    padding: 16px 32px;
  }
}

